html {
  box-sizing: border-box; }

*, *:before, *:after {
  box-sizing: inherit; }

body, ul, li {
  margin: 0;
  padding: 0; }

input {
  border-style: none;
  background: transparent;
  outline: none; }

button {
  padding: 0;
  background: none;
  border: none;
  outline: none; }

* {
  margin: 0;
  padding: 0;
  font-size: inherit;
  color: inherit;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  -webkit-font-smoothing: antialiased; }

*:focus {
  outline: none; }

html,
body {
  min-height: 100%;
  width: 100%; }

a {
  text-decoration: none; }

a:hover {
  text-decoration: underline; }
